
@font-face {
    font-family: "PingFang SC";
    src: url('./assets/PingFang-SC-Regular.eot');
}
@font-face
  {
    font-family: "PingFang SC";
    src: url('./assets/PingFang Bold.eot');
    font-weight:bold;
}
*{
  margin: 0;
  padding: 0;
}
.clearfix::after{
  display: block;
  clear: both;
  height: 0;
  content: '';
}
html{
  /*background: rgba(238, 242, 247, 0.68);*/
  /*background: #f2f2f2;*/
  background: #ffffff;
  margin-top: 60px;
  margin-bottom: 60px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}



